<template>
  <div class="container svg-box">
	<div class="avatar-box" @click="changeImg" v-html="avatar">
	</div>
  </div>
</template>
<script>
import { reactive, toRefs, computed, watch} from "vue";
import {useStore} from "vuex";
import { useRoute } from "vue-router";
import config from "@/utils/config";
import nftAbi from "@/utils/nftAbi";
import { saveSvgAsPng }  from 'save-svg-as-png';

export default {
  setup () {
     const store = useStore();
	 const route = useRoute()
     const data = reactive({
       web3: computed(() => store.state.provider),
	   rate: computed(() => store.state.rate),
       address: computed(() => store.state.accounts[0]),
	   avatar: '',
	   tokenId: route.params.id,
	   initContract: () => {
		 data.nftContract = new data.web3.eth.Contract(nftAbi, config.nftAddress)
		 data.initData()
	   },
	   initData: () => {
		 data.getAvatar()
	   },
	   changeImg: () => {
		let svg = document.querySelector(`.avatar-box svg` );
		saveSvgAsPng(svg, 'my_svg');
	   },
	   getAvatar: async () => {
		let svg = await data.nftContract.methods.tokenURI(data.tokenId).call()
		data.avatar = svg
	   }
     })
     if (data.web3) {
		 data.initContract()
     }
     watch(() => data.web3, (val) => {
       if (val) {
		data.initContract()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss">
.avatar-box{
 svg{
  width: 100% !important;
  max-height: 240px !important;
 }}
</style>
<style lang="scss" scoped>
.container {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}
.avatar-box{
  width: 320px;
  margin: 0 auto;
}
</style>
